<template>
  <div class="p5ParticleSystemRepeller noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from '@/components/Header.vue';
import P5NavBar from '@/components/P5NavBar.vue';

export default {
  components: {
    Header,
    P5NavBar
  },
  data () {
    return {
      script: null,
      ps: null,
      canvas: null,
      particleSystem: null,
      repeller: null
    };
  },
  mounted () {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);

        this.particleSystem = new ParticleSystem(
          p.createVector(p.width / 2, p.height / 2)
        );

        this.repeller = new Repeller(p.width / 2, p.height / 2 + 150);
      };

      p.draw = () => {
        p.background(255);

        this.particleSystem.addParticle();

        let gravity = p.createVector(0, 0.01);
        this.particleSystem.applyForce(gravity);
        this.particleSystem.applyRepeller(this.repeller);

        this.particleSystem.run();
        this.repeller.display();
      };

      class ParticleSystem {
        constructor (location) {
          this.origin = location.copy();
          this.particles = [];
        }

        addParticle () {
          this.particles.push(new Particle(this.origin));
        }

        applyForce (f) {
          for (let i = 0; i < this.particles.length; i++) {
            this.particles[i].applyForce(f);
          }
        }

        applyRepeller (r) {
          for (let i = 0; i < this.particles.length; i++) {
            let force = r.repel(this.particles[i]);
            this.particles[i].applyForce(force);
          }
        }

        run () {
          for (let particle of this.particles) {
            particle.run();
          }

          // remove dead particles
          this.particles = this.particles.filter(
            particle => !particle.isDead()
          );
        }
      }

      class Particle {
        constructor (l) {
          this.location = l.copy();
          this.velocity = p.createVector(p.random(-3, 3), p.random(-2, 0));
          this.acceleration = p.createVector(0, 0.1);
          this.lifespan = 1.0;
          this.mass = 1;
        }

        run () {
          this.update();
          this.display();
        }

        update () {
          this.velocity.add(this.acceleration);
          this.location.add(this.velocity);
          this.lifespan -= 0.01;
        }

        applyForce (force) {
          let f = force.copy();
          f.div(this.mass);
          this.acceleration.add(f);
        }

        display () {
          p.stroke(`rgba(57, 81, 105, ${this.lifespan})`);
          p.strokeWeight(2);
          p.fill(`rgba(66, 185, 131, ${this.lifespan})`);
          p.ellipse(this.location.x, this.location.y, 32, 32);
        }

        isDead () {
          if (this.lifespan < 0.0) {
            return true;
          } else {
            return false;
          }
        }
      }

      class Repeller {
        constructor (x, y) {
          this.location = p.createVector(x, y);
          this.r = 10;
          this.strength = 100;
        }

        display () {
          p.stroke('#395169');
          p.fill(255);
          p.ellipse(this.location.x, this.location.y, this.r * 2, this.r * 2);
        }

        repel (p) {
          let dir = P5.Vector.sub(this.location, p.location);
          let d = dir.mag();
          dir.normalize();
          d = P5.prototype.constrain(d, 5, 100);
          let force = -1 * this.strength / (d * d);
          dir.mult(force);

          return dir;
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require('p5');
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>
